export default {
	props:{
		tabBars: Array,
		currentIdx:{
			type:Number,
			default:0
		}
	},
	data(){
		return{
			barClassArr:['two','three','four','five','six','seven'],
		}
	},
	computed:{
		barClass() {
			return this.barClassArr[this.tabBars.length-2]
		}
	},
	methods:{
		ontabtap(index) {
			this.$emit('ontabtap', index)
		}
	}
	
}
